.page-header {
    width: 100vw;
    height: 66px;
    position: fixed;
    background: rgb(0,0,0,0.3);
    z-index: 2;
    display: flex;
    left: 0;
    top: 0;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    overflow: hidden;
}